.customizationContainer {
  font-family: "Roboto", sans-serif;

  background-color: white;
  position: absolute;
  left: 50%;
  transform: translate(-50%, -50%);

  top: 50%;

  width: 400px;
  height: 550px;

  border: 1px solid rgba(0, 0, 0, 0.281);
  border-radius: 10px;

  display: flex;
  flex-direction: column;

  padding: 2rem;
  box-shadow: 0 6px 20px rgba(255, 255, 255, 0.274);
}

.optionGroup__Name_Exit {
  display: flex;
  flex-direction: row;
  align-items: center;

  margin-bottom: 20px;
}

.customizationContainer h1 {
  margin-right: auto;
  font-size: 25px;
}

.closeCustomization {
  width: 40px;
  height: 40px;
}

.optionContainer {
  margin-bottom: 10px;
}

.options {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  height: 40px;
  margin: 10px 0;
  padding: 1rem;

  background-color: rgb(245, 245, 245);
  outline: none;

  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 5px;

  font-size: 16px;
  color: #333;
}

.options:hover {
  cursor: pointer;
  background-color: #1e88e534;
  border: 1px solid rgba(0, 0, 0, 0.3);
}

.options:active {
  background-color: #1e88e56b;
  color: black;
}

.optionSelected {
  background-color: #1e88e534;
  border: 1px solid rgba(0, 0, 0, 0.3);
}

.optionName {
  margin-right: auto;
}

.optionContainer h2 {
  font-size: 18px;
}

.optionGroup__Buttons {
  display: flex;
  flex-direction: row;
  align-items: center;

  margin-top: auto;
}

.quantityGroup {
  display: flex;
  flex-direction: row;
  align-items: center;
  font-size: 20px;

  margin-right: 3rem;
}

.quantityGroup i {
  font-size: 18px;
}

.quantityGroup p {
  margin: 0 10px;
}

.optionGroup__RightAlign {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-left: auto;
}

.optionGroup__RightAlign p {
  font-size: 20px;
}

.addToOrderButton {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;

  width: 150px;
  height: 40px;
  border-radius: 500px;
  border-width: 0;

  font-size: 18px;
  margin-right: 20px;

  background-color: #1e88e534;
  border: 1px solid rgba(0, 0, 0, 0.3);
  color: #333333;

  transition: box-shadow, background-color 0.2s;

  cursor: pointer;
  outline: none;
}

.addToOrderButton:active {
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1) !important;

  background-color: #1e88e56b;
  color: black;
}

.addToOrderButton span {
  box-shadow: 0 0px 0px rgba(0, 0, 0, 0);
  transition: box-shadow 0.2s;
  /* margin-top: -1px; */
}