.addressPromptBackground {
  font-family: 'Montserrat', sans-serif;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  background-color: rgba(0,0,0, 90%);
  z-index: 1;
  align-items: center;
}
.addressPromptBox {
  position: absolute;
  left: 15%;
  right: 15%;
  top: 35%;
  bottom: 40%;
  display: flex;
  align-items: center;
  margin: auto;
  border-radius: 10px;
  background-image: linear-gradient(to left, #961717 0%, #c04545 51%, #6d1010 100%);
  padding: 4rem;
  width: 700px;
}

.addressPromptBox i{
  padding: 1rem;
  margin-left: 80px;
}

.addressPromptBox:hover {
  background-position: right center;
  text-align: center;
  transition: 0.7s;
  background-size: 200% auto;
  background-image: linear-gradient(to left, #961717 0%, #c04545 51%, #6d1010 100%);
}

.addressPromptBox button {
  text-align: center;
  padding: 1rem;

  color: blanchedalmond;
  background-image: linear-gradient(to left, #520f0f 0%, #c04545 51%, #6d1010 100%);
  padding: 1rem;
  font-size: 20px;
  border: none;

  border-width: 0;
  outline: none;
  border-radius: 3px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.6);
}

.addressPromptBox button:hover{
  background-position: right;
  text-align: center;
  transition: 1s;
  background-size: 300% auto;
  background-image: linear-gradient(to right, #642d2d 0%, #471d1d 51%, #361515 100%);
}

.addressPromptBox input{
  padding: 1rem;
  width: 300px;
  border-left: 2px solid rgba(0, 0, 0, 0.1);
  border-top: 2px solid rgba(0, 0, 0, 0.1);
  border-bottom: 2px solid rgba(0, 0, 0, 0.1);
  border-radius: 5px;
}